@use "sass:math";
//<ШРИФТЫ>===============================================================================================

//<Подключаем шрифты>=======================================================================================
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Raleway:wght@200;400;500;700&display=swap");
//</Подключаем шрифты>=======================================================================================

//<Шрифт по умолчанию>==========================================================================================
//
$fontFamily: "Raleway", sans-serif;
$fontSize: 16;
//</Шрифт по умолчанию>==========================================================================================

//<ПЕРЕМЕННЫЕ>==========================================================================================================
$maxWidth: 1280;
$maxWidthContainer: 1200;
$md1: $maxWidthContainer + 12;
$md2: 1024;
$md3: 768;
$md4: 550;

$mainColor: #f1f5f4;
$orangeColor: #cd4d31;
$backgorundColor: #3f5252;
$blackColor: #1c1b29;
$greyColor: #504f5e;

$PlayfairFont: "Playfair Display", serif;

//</ПЕРЕМЕННЫЕ>===========================================================================================================

//<ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>===============================================================================================
@import "null";
body {
  color: $mainColor;
  background-color: $mainColor;

  &._lock {
    overflow: hidden;
  }
}
//</ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>===============================================================================================

//<ОБОЛОЧКА>===========================================================================================================
.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  &._loaded {
    .mainblock__title,
    .mainblock__under-title,
    .mainblock__text,
    .mainblock__button {
      opacity: 1;
      transform: translateY(0px);
    }
  }
}
//</ОБОЛОЧКА>===========================================================================================================

//<ОСНОВНАЯ СЕТКА>===========================================================================================================
._container {
  max-width: $maxWidthContainer + px;

  margin: 0 auto;
  padding: 0px 15px;
}
//</ОСНОВНАЯ СЕТКА>===========================================================================================================

//<ПОДКЛЮЧЕНИЕ ФАЙЛОВ UI СТИЛЕЙ, ФОРМ И ГОТОВЫХ КЛАССОВ>====================================================================================================
@import "ui";
//</ПОДКЛЮЧЕНИЕ ФАЙЛОВ UI СТИЛЕЙ, ФОРМ И ГОТОВЫХ КЛАССОВ>====================================================================================================

//<ПОДКЛЮЧЕНИЕ ФАЙЛОВ СТИЛЕЙ СТРАНИЦ И ЭЛЕМЕНТОВ>====================================================================================================
@import "blocks/header";
@import "blocks/mainblock";
@import "blocks/nav";
@import "blocks/exhibitions";
@import "blocks/events";
@import "blocks/visit";
@import "blocks/news";
@import "blocks/subscribe";
@import "blocks/footer";
