.subscribe {
  background-color: $orangeColor;
  padding: 160px 0px;

  @media (max-width: 1100px) {
    padding: 100px 0px;
  }

  @media (max-width:$md3+px){
     padding: 50px 0px;
  }

  // .subscribe__container
  &__container {
  }

  // .subscribe__body
  &__body {
  }

  // .subscribe__title
  &__title {
    color: $mainColor;
    margin-bottom: 40px;

    @media (max-width: 500px) {
      font-size: 38px;
      margin-bottom: 30px;
    }
  }

  // .subscribe__row
  &__row {
    @media (min-width: 1100px) {
      display: flex;
      justify-content: space-between;
    }
  }

  // .subscribe__text
  &__text {
    max-width: 510px;
    line-height: 150%;

    @media (max-width: 1100px) {
      margin-bottom: 40px;
    }
  }

  // .subscribe__form
  &__form {
    margin-right: 120px;
  }
}

.form-subscribe {
  max-width: 352px;
  width: 100%;

  display: flex;

  // .form-subscribe__input
  &__input {
    border-bottom: 1px solid $mainColor;
    font-size: 16px;
  }

  // .form-subscribe__button
  &__button {
    margin-left: 10px;

    flex: 0 0 42px;
    height: 42px;

    border: 1px solid $mainColor;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;
    align-self: end;
  }
}
