.news {
  padding: 120px 0px;

  @media (max-width: 1024px) {
    padding: 80px 0px;
  }

  @media (max-width: $md3+px) {
    padding: 60px 0px;
  }

  // .news__container
  &__container {
  }

  // .news__body
  &__body {
  }

  // .news__title
  &__title {
    margin: 0px 0px 75px 200px;

    @media (max-width: $md3+px) {
      margin: 0px 0px 50px 0px;

      text-align: center;
    }
  }

  // .news__row
  &__row {
  }

  // .news__all
  &__all {
    text-align: right;
    margin-right: 100px;

    @media (max-width:$md3+px){
       margin: 0;
    }

    @media (max-width: $md4+px) {
      text-align: center;
      margin: 0;
    }
  }
}

.row-news {
  display: flex;
  flex-wrap: wrap;

  margin: 0px -15px;

  // .row-news__column
  &__column {
    margin-bottom: 80px;
    padding: 0px 15px;

    flex: 0 1 33.333%;

    @media (max-width: $md2+px) {
      flex: 0 1 50%;
    }

    @media (max-width: $md4+px) {
      flex: 1 1 auto;
      margin-bottom: 60px;
    }
  }

  // .row-news__item
  &__item {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  // .row-news__image
  &__image {
    display: block;

    padding: 0px 0px 100% 0px;
    margin-bottom: 36px;

    transition: transform 0.3s;

    @media (any-hover: hover) {
      &:hover {
        transform: scale(1.05);
      }
    }
  }

  // .row-news__subtitle
  &__subtitle {
    margin-bottom: 8px;

    font-weight: 200;
    font-size: 14px;
    color: $blackColor;
  }

  // .row-news__title
  &__title {
    font-weight: 700;
    font-size: 20px;
    color: $blackColor;
    font-family: $PlayfairFont;
    margin-bottom: 15px;
  }

  // .row-news__text
  &__text {
    line-height: 150%;
    color: $greyColor;
  }
}
